  position: relative; }
  .navbar__link, .navbar__link:active, .navbar__link:link, .navbar__link:visited, .navbar__link:focus {
    display: block;
    padding: 1.8rem 0.5rem 0.2rem;
    border-top: 2px solid transparent;
    transition: border 0.3s ease 0s, color 0.3s ease 0s; }
  .navbar__link:hover {
    border-top: 2px solid #2179d6;
    text-decoration: none;
    color: #2179d6; }
  .navbar__top-menu {
    display: flex;
    flex-flow: row nowrap;
    list-style: none;
    margin: 0 0 0 auto;
    padding: 0; }
  .navbar__active {
    font-weight: 500; }
  .navbar__brand {
    font-weight: bold;
    font-size: 1.5rem;
    width: 25%;
    margin-top: 1rem;
    color: black;
    opacity: 0.85;
    transition: opacity 0.3s ease 0s; }
    .navbar__brand:hover {
      opacity: 1; }
    .navbar__brand-link, .navbar__brand-link:active, .navbar__brand-link:link, .navbar__brand-link:visited, .navbar__brand-link:focus {
      color: black;
      opacity: 0.85; }
    .navbar__brand-logo {
      width: 60px; }
  .navbar__hamburger {
    font-size: 2rem;
    display: none;
    margin: 0;
    padding: 0; }
  .navbar__cart {
    margin-left: 2rem;
    font-size: 2rem;
    margin-top: 1.5rem;
    transition: opacity 0.3s ease 0s !important;
    opacity: 0.85; }
    .navbar__cart:hover {
      opacity: 1; }

@media only screen and (max-width: 992px) {
  .navbar {
    flex-flow: column;
    align-items: center; }
    .navbar__top-menu {
      margin: 0.5rem 0 0 -0.5rem; }
    .navbar__brand {
      width: auto; }
    .navbar__link, .navbar__link:active, .navbar__link:link, .navbar__link:visited, .navbar__link:focus {
      display: block;
